.Button {
    background-color: transparent;
    border: 1px solid black;
    width: 120px;
    height: 50px;
    color: white;
    outline: none;
    cursor: pointer;
    font: inherit;
    padding: 10px;
    margin: 10px;
    font-weight: bold;
    border-radius: 10%;
}

.Button:hover{
    color: white;
    -webkit-animation: popup 0.2s ease-in;
            animation: popup 0.2s ease-in;
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
}

.Button:hover .Loader{
    color:white;
}

.Button:disabled{
    color: #ccc;
    background-color: #eee;
    cursor: not-allowed;
    -webkit-animation: none;
            animation: none;
}

.Success {
    background-color: #5C9210;
}

.Danger {
    background-color: #944317;
}

@-webkit-keyframes popup {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100%{
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
}

@keyframes popup {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100%{
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
}


